import React from "react";
import { Col, Row, Container, Jumbotron } from "reactstrap";

const NoMatch = () => (
  <Container>
    <Row>
      <Col md="12">
        <Jumbotron>
          <h1>404 Page Not Found</h1>
          <h1>
            <span role="img" aria-label="Face With Rolling Eyes Emoji">
              🙄
            </span>
          </h1>
        </Jumbotron>
      </Col>
    </Row>
  </Container>
);

export default NoMatch;



